    .offer {
        ul {
            padding-left: 20px;
            li.leaf {
                list-style: none;
                span {
                    font-size: 20px;
                    &:before {
                        content: '';
                        display: inline-block;
                        width: 50px;
                        height: 50px;
                        background: url('https://go.dreamspring.org/hubfs/icon-briefcase.png');
                        background-size: cover;
                        margin-left: -50px;
                        position: relative;
                        top: 20px;
                    }
                }
                
            }
        }
    }
    .member-form {
        #hs_form_target_apply_form {
            background: #c9f0fb;
            padding: 15px;
            font-size: 13px;
            ul.inputs-list {
                padding-left: 0;
                li {
                    list-style: none;
                }
            }
            input[type="radio"]{
                display: inline-block;
                width: 25px !important;
            }
        }
        h3 {
            background: #8fc741;
            color: $white;
            font-size: 13px;
            text-align: center;
